	methods: {
		init(){
			if(!this.id){
				uni.showModal({
					titel:'提示',
					content:'您的资料不完整，请先完善',
					cancelText:'去完善',
					cancelColor:'#007AFF',
					confirmText:'先看看',
					confirmColor:'#007AFF',
					success:res =>{
						if (res.confirm) {
							console.log('用户点击先看看');
						} else if (res.cancel) {
							console.log('用户点击去完善');
							uni.navigateTo({
